import { Component, OnInit, ViewChild, inject } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ICargo } from "../../interface/cargo";
import { Router } from "@angular/router";
import { CargosService } from "../../service/cargos.service";
import Swal from "sweetalert2";
import { isCaracter } from "src/app/constantes/constantesproyecto";
import { DataTableDirective } from "angular-datatables";
import { Subject } from "rxjs";
import { IAdministrador } from "src/app/pages/administrador/interface/administrador";
import { NgbModal } from "@ng-bootstrap/ng-bootstrap";

@Component({
  selector: "app-lista-cargos",
  templateUrl: "./lista-cargos.component.html",
  styleUrls: ["./lista-cargos.component.scss"],
})
export class ListaCargosComponent implements OnInit {
  breadCrumbItems: Array<{}>;
  cargos: ICargo[] = [];
  administradores: IAdministrador[];

  formCargo!: FormGroup;
  cargoe: ICargo;

  /**DataTables */
  @ViewChild(DataTableDirective, { static: false })
  dtElement: DataTableDirective;
  dtOptions: any = {};
  dtTrigger: Subject<ICargo> = new Subject<ICargo>();

  constructor(
    private cargoService: CargosService,
    private fb: FormBuilder,
    private modalService: NgbModal,
    private router: Router
  ) {
    this.formCargo = this.iniciarFormulario();
  }

  ngOnInit(): void {
    this.breadCrumbItems = [
      { label: "Cargo" },
      { label: "Registrar", active: true },
    ];
    this.dtOptions = {
      columnDefs: [
        // { className: "", targets: [0, 1, 2] },
        { orderable: false, targets: [0, 2] },
        { searchable: false, targets: [2] },
        { responsivePriority: 1, targets: -1 },

      ],
      lengthMenu: [5, 10, 20, 50],
      destroy: true,
      language: {
        url: "//cdn.datatables.net/plug-ins/1.13.5/i18n/es-ES.json",
        lengthMenu: "Mostrar _MENU_ registros",
        zeroRecords: "Ningún dato encontrado",
      },
      pagingType: "full_numbers",
      responsive: true,
    };
    this.getPageCargos();
  }
  ngOnDestroy() {
    //para destruir la peticion
    this.dtTrigger.unsubscribe();
  }

  setDatos(cargoe: ICargo) {
    this.cargoe = cargoe;
  }

  openModalA(content) {
    this.modalService.open(content, { keyboard: false, scrollable: true, size: 'lg'});
  }

  private iniciarFormulario(): FormGroup {
    return this.fb.group({
      nombrecargo: [
        "",
        [
          Validators.required,
          Validators.minLength(4),
          Validators.pattern(isCaracter),
        ],
      ],
      id: [""],
    });
  }

  esCampoValido(campo: string) {
    const validarCampo = this.formCargo.get(campo);
    return !validarCampo?.valid && validarCampo?.touched
      ? "is-invalid"
      : validarCampo?.touched
      ? "is-valid"
      : "";
  }

  getPageCargos() {
    //obtener los datos
    this.cargoService.mostrarCargoList().subscribe((resp: any) => {
      this.cargos = resp;
      this.dtTrigger.next(null);
      this.cargoe = null;
    });
    this.cargoService.AdministradoresListaCargo().subscribe((resp: any) => {
      this.administradores = resp;
    });
  }

  guardarCargos() {
    //funciona para guardar y editar
    if (this.formCargo.valid) {
      if (this.cargoe != null) {
        this.editandoCargo();
      } else {
        this.registrandoCargoForm();
      }

    } else {
      Swal.fire({
        position: "center",
        title: "Faltan datos en el formulario",
        text: "completar campos requeridos",
        icon: "warning",
      });
    }
  }

  registrandoCargoForm() {
    const cargo = this.formCargo.value;
    const cargovalid=this.formCargo.get('nombrecargo').value;
    const v=this.existe(cargovalid);
    if(v){
      Swal.fire('Error', 'Ya existe el cargo', 'warning');
    }else{
      this.cargoService.nuevoCargo(cargo).subscribe(
        (resp: any) => {
          if (resp) {
              Swal.fire("Éxito", "Almacenado correctamente", "success");
              this.formCargo.reset();
              this.reload();
          }
        },
        (err: any) => {
          //consoleerro
          Swal.fire("Ocurrio un problema", "", "warning");
        }
      );
    }

  }

  editandoCargo() {
    const cargoe = this.formCargo.value;
    this.cargoService.editarCargo(cargoe).subscribe(
      (resp: any) => {
        if (resp) {
          Swal.fire("Éxito", "Modificado correctamente", "success");
          this.formCargo.reset();
          this.reload();
        }
      },
      (err: any) => {
        //consoleerro
        Swal.fire("Ocurrio un problema", "", "warning");
      }
    );
  }

  eliminandoconAlert(x: ICargo): void {
    Swal.fire({
      title: '¿Está seguro de eliminar? ',
      text: `Esto no se puede revertir`,
      icon: 'question',
      showCancelButton: true,
      confirmButtonText: 'Sí, borrar',
      cancelButtonText:'Cancelar'
    }).then((result) => {
      if (result.isConfirmed) {
        const v = this.verificar(x.id)
        if (v) {
          Swal.fire('Error', 'El registro no puede ser eliminado', 'warning');
        } else {
          this.cargoService.deleteCargo(x).subscribe(response => {
            Swal.fire('Eliminado', 'El registro ha sido eliminado', 'success');
            this.reload()
          });
        }
      } else if (result.isDenied) {
        Swal.fire('Cambios no aplicados', '', 'info')
      }
    });
  }

  verificar(id: number): boolean {
    return this.administradores.some(admin => admin.cargo.id === id);
  }

  existe(nombre:string):boolean{
    return this.cargos.some(cargo=>cargo.nombrecargo===nombre);
  }

  mostrarRecargando() {
    //recarga los datos
    let currentUrl = this.router.url;
    this.router.routeReuseStrategy.shouldReuseRoute = () => false;
    this.router.onSameUrlNavigation = "reload";
    this.router.navigate([currentUrl]);
  }

  /**Para recargar */
  reload() {
    this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
      this.cargos = [];
      dtInstance.destroy();
      this.getPageCargos();
    });
  }
}
