import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ICargo } from '../interface/cargo';
import { Observable } from 'rxjs';
import { IAdministrador } from '../../administrador/interface/administrador';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class CargosService {
  url = `${environment.urlG}`


  constructor(private http: HttpClient) { }

  public mostrarCargoList(): Observable<any>  {
    return this.http.get<ICargo[]>(`${this.url}/cargo/listaCargos`);

  }
  public nuevoCargo(cargo:ICargo):any{
    return this.http.post(`${this.url}/cargo/guardar`,cargo);
  }

  public deleteCargo(id:ICargo):Observable<ICargo>{
    return this.http.delete<ICargo>(`${this.url}/cargo/${id.id}`);
  }

  public editarCargo(cargo:ICargo):any{
    return this.http.put(`${this.url}/cargo/modificar`,cargo);
  }

  public buscar(filtro: string, page: number = 0, size: number = 3): Observable<any> {
    return this.http.get<any>(`${this.url}/cargo/buscar/${filtro}` + '?' + `page=${page}&size=${size}`)
  }

  public AdministradoresListaCargo(): Observable<IAdministrador[]>{
    return this.http.get<IAdministrador[]>(`${this.url}/administrador/listaAdmin`);
  }
}
